import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import './css/Msg.css'
import Header from '../../component/Header'
import { InputItem } from 'antd-mobile';
import 'antd-mobile/lib/date-picker/style/css';

class Msg extends Component {
    constructor(props) {
        super(props)
        this.state = {
            shopCar: null,
            district: [
                {
                    label: '...',
                    value: 1
                }, {
                    label: '1',
                    value: 1
                }, {
                    label: '2',
                    value: 1
                }, {
                    label: '3',
                    value: 1
                }, {
                    label: '4',
                    value: 1
                }, {
                    label: '5',
                    value: 1
                }, {
                    label: '6',
                    value: 1
                }, {
                    label: '7',
                    value: 1
                }, {
                    label: '8',
                    value: 1
                }, {
                    label: '9',
                    value: 1
                }, {
                    label: '10',
                    value: 1
                }, {
                    label: '11',
                    value: 1
                }, {
                    label: '12',
                    value: 1
                }, {
                    label: '13',
                    value: 1
                }, {
                    label: '14',
                    value: 1
                }, {
                    label: '15',
                    value: 1
                }],
            anchorEl: null,
            flog: 'block',
            value: '',
            shopCarNum:0
        }
    }

    render() {
        let { shopCar, district, anchorEl, value,shopCarNum } = this.state;
        console.log(shopCar)
        return (
            <div className='Msg'>
                <Header />
                <div className='guestMsg'>
                    <div className='desk'><select className='deskNUm' onChange={this.optionChang} defaultValue={'...'}>
                        {
                            district.map((v, i) => {
                                return <option value={v.label} key={i} >{v.label}</option>
                            })
                        }
                    </select>

                    </div>
                    <div className='peopleNum'>
                        <InputItem placeholder={value} maxLength={3} onBlur={(e) => this.Blur(e)} updatePlaceholder={true} type='number' />
                    </div>
                </div>
                <div className='isMenu'>
                    <h2></h2>
                    <ul className='isMenuTit'>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                    <div className='MenuList'>
                        {
                            shopCar && shopCar.length > 0 && shopCar.map((v, i) => {
                                return <ul className='isMenuTit' key={i}>
                                    <li>{v.name}</li>
                                    <li>{v.specfoods[0].price}</li>
                                    <li>{v.is_featured}</li>
                                </ul>
                            })
                        }
                    </div>
                </div>
                <div className='addMenu' onClick={()=>this.addMenu()}><i className='iconfont canpan'></i></div>
                <div className='Footer'>
                    <div className='all'>
                        <p className='allnum'><span>{shopCarNum}</span></p>
                        <p className='allpic'><span>1</span></p>
                    </div>
                    <a className='goPay'></a>
                </div>
            </div>
        );
    }
    optionChang = (e) => {
        console.log(e.target.value)
    };
    Blur(e) {
        console.log(e)
    }
    addMenu(){
        this.props.history.push('/')
    }
    componentDidMount() {
        let shopCar = JSON.parse(localStorage.getItem('shopcar'));
        if (shopCar == null) {
            this.props.history.push('/')
        }
        this.setState({
            shopCar,
            shopCarNum:shopCar.length
        })
    }
}

export default withRouter(Msg);


